<template><section>

<h1>Alert 警告</h1>
<hr>
<p>用于页面中展示重要的提示信息。</p>
<h3>基本用法</h3>
<p>页面中的非浮层元素，不会自动消失。</p>
<div class="dome-alert demo-block">
  <w-alert type="success">成功提示的文案</w-alert>
  <w-alert type="info">消息提示的文案</w-alert>
  <w-alert type="warning" title="警告提示的文案"></w-alert>
  <w-alert type="error" title="错误提示的文案"></w-alert>
</div>

                    <demo-block>
                        <div slot="code" data-desc="%3Ctemplate%3E%0A%20%20%3Cdiv%3E%0A%20%20%20%20%3Cw-alert%20type%3D%22success%22%3E%E6%88%90%E5%8A%9F%E6%8F%90%E7%A4%BA%E7%9A%84%E6%96%87%E6%A1%88%3C%2Fw-alert%3E%0A%20%20%20%20%3Cw-alert%20type%3D%22info%22%3E%E6%B6%88%E6%81%AF%E6%8F%90%E7%A4%BA%E7%9A%84%E6%96%87%E6%A1%88%3C%2Fw-alert%3E%0A%20%20%20%20%3Cw-alert%20type%3D%22warning%22%20title%3D%22%E8%AD%A6%E5%91%8A%E6%8F%90%E7%A4%BA%E7%9A%84%E6%96%87%E6%A1%88%22%3E%3C%2Fw-alert%3E%0A%20%20%20%20%3Cw-alert%20type%3D%22error%22%20title%3D%22%E9%94%99%E8%AF%AF%E6%8F%90%E7%A4%BA%E7%9A%84%E6%96%87%E6%A1%88%22%3E%3C%2Fw-alert%3E%0A%20%20%3C%2Fdiv%3E%0A%3C%2Ftemplate%3E%0A%0A"></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">w-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"success"</span>&gt;</span>成功提示的文案<span class="hljs-tag">&lt;/<span class="hljs-name">w-alert</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">w-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"info"</span>&gt;</span>消息提示的文案<span class="hljs-tag">&lt;/<span class="hljs-name">w-alert</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">w-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"warning"</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"警告提示的文案"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">w-alert</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">w-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"error"</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"错误提示的文案"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">w-alert</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>带有 icon</h3>
<p>表示某种状态时提升可读性。</p>
<div class="dome-alert demo-block">
  <w-alert type="success" show-icon="">成功提示的文案(暂无对应图标)</w-alert>
  <w-alert type="info" show-icon="">消息提示的文案(暂无对应图标)</w-alert>
  <w-alert type="warning" show-icon="">警告提示的文案(暂无对应图标)</w-alert>
  <w-alert type="error" show-icon="">错误提示的文案(暂无对应图标)</w-alert>
</div>

                    <demo-block>
                        <div slot="code" data-desc="%3Ctemplate%3E%0A%20%20%3Cdiv%3E%0A%20%20%20%20%3Cw-alert%20type%3D%22success%22%20show-icon%3E%E6%88%90%E5%8A%9F%E6%8F%90%E7%A4%BA%E7%9A%84%E6%96%87%E6%A1%88%3C%2Fw-alert%3E%0A%20%20%20%20%3Cw-alert%20type%3D%22info%22%20show-icon%3E%E6%B6%88%E6%81%AF%E6%8F%90%E7%A4%BA%E7%9A%84%E6%96%87%E6%A1%88%3C%2Fw-alert%3E%0A%20%20%20%20%3Cw-alert%20type%3D%22warning%22%20show-icon%3E%E8%AD%A6%E5%91%8A%E6%8F%90%E7%A4%BA%E7%9A%84%E6%96%87%E6%A1%88%3C%2Fw-alert%3E%0A%20%20%20%20%3Cw-alert%20type%3D%22error%22%20show-icon%3E%E9%94%99%E8%AF%AF%E6%8F%90%E7%A4%BA%E7%9A%84%E6%96%87%E6%A1%88%3C%2Fw-alert%3E%0A%20%20%3C%2Fdiv%3E%0A%3C%2Ftemplate%3E%0A%0A%0A"></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">w-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"success"</span> <span class="hljs-attr">show-icon</span>&gt;</span>成功提示的文案<span class="hljs-tag">&lt;/<span class="hljs-name">w-alert</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">w-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"info"</span> <span class="hljs-attr">show-icon</span>&gt;</span>消息提示的文案<span class="hljs-tag">&lt;/<span class="hljs-name">w-alert</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">w-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"warning"</span> <span class="hljs-attr">show-icon</span>&gt;</span>警告提示的文案<span class="hljs-tag">&lt;/<span class="hljs-name">w-alert</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">w-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"error"</span> <span class="hljs-attr">show-icon</span>&gt;</span>错误提示的文案<span class="hljs-tag">&lt;/<span class="hljs-name">w-alert</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>


</code></pre>
</div></demo-block>
<h3>文字居中</h3>
<p>使用 <code v-pre="" class="code_inline">center</code> 属性让文字水平居中。</p>
<div class="dome-alert demo-block">
  <w-alert type="success" center="" show-icon="">成功提示的文案</w-alert>
  <w-alert type="info" center="" show-icon="">消息提示的文案</w-alert>
  <w-alert type="warning" center="" show-icon="">警告提示的文案</w-alert>
  <w-alert type="error" center="" show-icon="">错误提示的文案</w-alert>
</div>

                    <demo-block>
                        <div slot="code" data-desc="%3Ctemplate%3E%0A%20%20%3Cdiv%3E%0A%20%20%20%20%3Cw-alert%20type%3D%22success%22%20center%20show-icon%3E%E6%88%90%E5%8A%9F%E6%8F%90%E7%A4%BA%E7%9A%84%E6%96%87%E6%A1%88%3C%2Fw-alert%3E%0A%20%20%20%20%3Cw-alert%20type%3D%22info%22%20center%20show-icon%3E%E6%B6%88%E6%81%AF%E6%8F%90%E7%A4%BA%E7%9A%84%E6%96%87%E6%A1%88%3C%2Fw-alert%3E%0A%20%20%20%20%3Cw-alert%20type%3D%22warning%22%20center%20show-icon%3E%E8%AD%A6%E5%91%8A%E6%8F%90%E7%A4%BA%E7%9A%84%E6%96%87%E6%A1%88%3C%2Fw-alert%3E%0A%20%20%20%20%3Cw-alert%20type%3D%22error%22%20center%20show-icon%3E%E9%94%99%E8%AF%AF%E6%8F%90%E7%A4%BA%E7%9A%84%E6%96%87%E6%A1%88%3C%2Fw-alert%3E%0A%20%20%3C%2Fdiv%3E%0A%3C%2Ftemplate%3E%0A%0A%0A"></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">w-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"success"</span> <span class="hljs-attr">center</span> <span class="hljs-attr">show-icon</span>&gt;</span>成功提示的文案<span class="hljs-tag">&lt;/<span class="hljs-name">w-alert</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">w-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"info"</span> <span class="hljs-attr">center</span> <span class="hljs-attr">show-icon</span>&gt;</span>消息提示的文案<span class="hljs-tag">&lt;/<span class="hljs-name">w-alert</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">w-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"warning"</span> <span class="hljs-attr">center</span> <span class="hljs-attr">show-icon</span>&gt;</span>警告提示的文案<span class="hljs-tag">&lt;/<span class="hljs-name">w-alert</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">w-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"error"</span> <span class="hljs-attr">center</span> <span class="hljs-attr">show-icon</span>&gt;</span>错误提示的文案<span class="hljs-tag">&lt;/<span class="hljs-name">w-alert</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>


