</div></demo-block>
<h3>自定义关闭按钮</h3>
<p>自定义关闭按钮为文字或其他符号。</p>
<div class="dome-alert demo-block">
  <w-alert type="success" :closable="false">不可关闭的 alert</w-alert>
  <w-alert type="info" close-text="知道了">自定义关闭的 alert</w-alert>
  <w-alert type="warning" @close="hello">设置了回调的 alert</w-alert>
</div>

                    <demo-block>
                        <div slot="code" data-desc="%3Ctemplate%3E%0A%20%20%3Cdiv%20class%3D%22dome-alert%20demo-block%22%3E%0A%20%20%20%20%3Cw-alert%20type%3D%22success%22%20%3Aclosable%3D%22false%22%3E%E4%B8%8D%E5%8F%AF%E5%85%B3%E9%97%AD%E7%9A%84%20alert%3C%2Fw-alert%3E%0A%20%20%20%20%3Cw-alert%20type%3D%22info%22%20close-text%3D%22%E7%9F%A5%E9%81%93%E4%BA%86%22%3E%E8%87%AA%E5%AE%9A%E4%B9%89%E5%85%B3%E9%97%AD%E7%9A%84%20alert%3C%2Fw-alert%3E%0A%20%20%20%20%3Cw-alert%20type%3D%22warning%22%20@close%3D%22hello%22%3E%E8%AE%BE%E7%BD%AE%E4%BA%86%E5%9B%9E%E8%B0%83%E7%9A%84%20alert%3C%2Fw-alert%3E%0A%20%20%3C%2Fdiv%3E%0A%3C%2Ftemplate%3E%0A%0A%0A"></div>
