    <span class="hljs-tag">&lt;<span class="hljs-name">w-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"success"</span> <span class="hljs-attr">:closable</span>=<span class="hljs-string">"false"</span>&gt;</span>不可关闭的 alert<span class="hljs-tag">&lt;/<span class="hljs-name">w-alert</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">w-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"info"</span> <span class="hljs-attr">close-text</span>=<span class="hljs-string">"知道了"</span>&gt;</span>自定义关闭的 alert<span class="hljs-tag">&lt;/<span class="hljs-name">w-alert</span>&gt;</span>
