  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>


</code></pre>
</div></demo-block>
<div class="dome-alert demo-block">
  <w-alert type="success" description="这是一句绕口令：黑灰化肥会挥发发灰黑化肥挥发；灰黑化肥会挥发发黑灰化肥发挥。 黑灰化肥会挥发发灰黑化肥黑灰……">带辅助性文字介绍</w-alert>
  <w-alert type="info" show-icon="" description="这是一句绕口令：黑灰化肥会挥发发灰黑化肥挥发；灰黑化肥会挥发发黑灰化肥发挥。 黑灰化肥会挥发发灰黑化肥黑灰……">带有 icon 和辅助性文字介绍</w-alert>
</div>

                    <demo-block>
                        <div slot="code" data-desc="%3Ctemplate%3E%0A%20%20%3Cdiv%20class%3D%22dome-alert%20demo-block%22%3E%0A%20%20%20%20%3Cw-alert%20type%3D%22success%22%0A%20%20%20%20%20%20%20%20%20%20%20%20%20description%3D%22%E8%BF%99%E6%98%AF%E4%B8%80%E5%8F%A5%E7%BB%95%E5%8F%A3%E4%BB%A4%EF%BC%9A%E9%BB%91%E7%81%B0%E5%8C%96%E8%82%A5%E4%BC%9A%E6%8C%A5%E5%8F%91%E5%8F%91%E7%81%B0%E9%BB%91%E5%8C%96%E8%82%A5%E6%8C%A5%E5%8F%91%EF%BC%9B%E7%81%B0%E9%BB%91%E5%8C%96%E8%82%A5%E4%BC%9A%E6%8C%A5%E5%8F%91%E5%8F%91%E9%BB%91%E7%81%B0%E5%8C%96%E8%82%A5%E5%8F%91%E6%8C%A5%E3%80%82%20%E9%BB%91%E7%81%B0%E5%8C%96%E8%82%A5%E4%BC%9A%E6%8C%A5%E5%8F%91%E5%8F%91%E7%81%B0%E9%BB%91%E5%8C%96%E8%82%A5%E9%BB%91%E7%81%B0%E2%80%A6%E2%80%A6%22%3E%E5%B8%A6%E8%BE%85%E5%8A%A9%E6%80%A7%E6%96%87%E5%AD%97%E4%BB%8B%E7%BB%8D%3C%2Fw-alert%3E%0A%20%20%20%20%3Cw-alert%20type%3D%22info%22%20show-icon%0A%20%20%20%20%20%20%20%20%20%20%20%20%20description%3D%22%E8%BF%99%E6%98%AF%E4%B8%80%E5%8F%A5%E7%BB%95%E5%8F%A3%E4%BB%A4%EF%BC%9A%E9%BB%91%E7%81%B0%E5%8C%96%E8%82%A5%E4%BC%9A%E6%8C%A5%E5%8F%91%E5%8F%91%E7%81%B0%E9%BB%91%E5%8C%96%E8%82%A5%E6%8C%A5%E5%8F%91%EF%BC%9B%E7%81%B0%E9%BB%91%E5%8C%96%E8%82%A5%E4%BC%9A%E6%8C%A5%E5%8F%91%E5%8F%91%E9%BB%91%E7%81%B0%E5%8C%96%E8%82%A5%E5%8F%91%E6%8C%A5%E3%80%82%20%E9%BB%91%E7%81%B0%E5%8C%96%E8%82%A5%E4%BC%9A%E6%8C%A5%E5%8F%91%E5%8F%91%E7%81%B0%E9%BB%91%E5%8C%96%E8%82%A5%E9%BB%91%E7%81%B0%E2%80%A6%E2%80%A6%22%3E%E5%B8%A6%E6%9C%89%20icon%20%E5%92%8C%E8%BE%85%E5%8A%A9%E6%80%A7%E6%96%87%E5%AD%97%E4%BB%8B%E7%BB%8D%3C%2Fw-alert%3E%0A%20%20%3C%2Fdiv%3E%0A%3C%2Ftemplate%3E%0A%0A%0A"></div>
