<template><section><h1>LoadingBar 加载进度条</h1>
<hr>
<p>全局创建了一个用于显示页面加载、异步请求的加载进度条。
因为可复用性的关系，<code v-pre="" class="code_inline">LoadingBar</code> 只会全局创建一个实例，而且在 <code v-pre="" class="code_inline">Vue.prototype</code> 中添加了全局对象 <code v-pre="" class="code_inline">$Loading</code>，可以直接通过 <code v-pre="" class="code_inline">this.$Loading</code> 操作实例</p>
<h3>基础用法</h3>
<p>通过调用 $Loading 提供的三种方法来控制全局的加载进度条 <code v-pre="" class="code_inline">start()</code>、<code v-pre="" class="code_inline">end()</code>、<code v-pre="" class="code_inline">error()</code></p>

<div class="demo-block">
   <w-button @click="start">开始</w-button>
   <w-button @click="end">结束</w-button>
   <w-button @click="error">错误</w-button>
</div>

                    <demo-block>
                        <div slot="code" data-desc="%3Ctemplate%3E%0A%20%20%3Cw-button%20@click%3D%22start%22%3E%E5%BC%80%E5%A7%8B%3C%2Fw-button%3E%0A%20%20%3Cw-button%20@click%3D%22end%22%3E%E7%BB%93%E6%9D%9F%3C%2Fw-button%3E%0A%20%20%3Cw-button%20@click%3D%22error%22%3E%E9%94%99%E8%AF%AF%3C%2Fw-button%3E%0A%3C%2Ftemplate%3E%0A%3Cscript%3E%0A%20%20export%20default%7B%0A%20%20%20%20methods%3A%20%7B%0A%20%20%20%20%20%20start%20()%20%7B%0A%20%20%20%20%20%20%20%20this.%24loading.start()%0A%20%20%20%20%20%20%7D%2C%0A%20%20%20%20%20%20end%20()%20%7B%0A%20%20%20%20%20%20%20%20this.%24loading.end()%0A%20%20%20%20%20%20%7D%2C%0A%20%20%20%20%20%20error%20()%20%7B%0A%20%20%20%20%20%20%20%20this.%24loading.error()%0A%20%20%20%20%20%20%7D%0A%20%20%20%20%7D%0A%20%20%7D%0A%3C%2Fscript%3E%0A"></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">w-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"start"</span>&gt;</span>开始<span class="hljs-tag">&lt;/<span class="hljs-name">w-button</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">w-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"end"</span>&gt;</span>结束<span class="hljs-tag">&lt;/<span class="hljs-name">w-button</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">w-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"error"</span>&gt;</span>错误<span class="hljs-tag">&lt;/<span class="hljs-name">w-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span>{
    <span class="hljs-attr">methods</span>: {
      start () {
        <span class="hljs-keyword">this</span>.$loading.start()
      },
      end () {
        <span class="hljs-keyword">this</span>.$loading.end()
      },
      error () {
        <span class="hljs-keyword">this</span>.$loading.error()
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>常规配置</h3>
<p>提供 LoadingBar 的全局配置，使用方法如下：</p>
<div class="demo-block">
   <w-button @click="setSpeed">设置speed速度</w-button>
   <w-button @click="setSpinner">设置spinner动画效果</w-button>
   <w-button @click="setPercentNum">设置percentNum每次加载的比例</w-button>
   <w-button @click="setShowSpinner">设置是否显示spinner</w-button>
</div>

                    <demo-block>
                        <div slot="code" data-desc="%3Ctemplate%3E%0A%20%20%3Cw-button%20@click%3D%22setSpeed%22%3E%E8%AE%BE%E7%BD%AEspeed%E9%80%9F%E5%BA%A6%3C%2Fw-button%3E%0A%20%20%3Cw-button%20@click%3D%22setSpinner%22%3E%E8%AE%BE%E7%BD%AEspinner%E5%8A%A8%E7%94%BB%E6%95%88%E6%9E%9C%3C%2Fw-button%3E%0A%20%20%3Cw-button%20@click%3D%22setPercentNum%22%3E%E8%AE%BE%E7%BD%AEpercentNum%E6%AF%8F%E6%AC%A1%E5%8A%A0%E8%BD%BD%E7%9A%84%E6%AF%94%E4%BE%8B%3C%2Fw-button%3E%0A%20%20%3Cw-button%20@click%3D%22setShowSpinner%22%3E%E8%AE%BE%E7%BD%AE%E6%98%AF%E5%90%A6%E6%98%BE%E7%A4%BAspinner%3C%2Fw-button%3E%0A%3C%2Ftemplate%3E%0A%3Cscript%3E%0A%20%20export%20default%7B%0A%20%20%20%20methods%3A%20%7B%0A%20%20%20%20%20setSpeed%20()%20%7B%0A%20%20%20%20%20%20%20this.%24loading.config(%7B%0A%20%20%20%20%20%20%20%20%20speed%3A%2010%0A%20%20%20%20%20%20%20%7D)%3B%0A%20%20%20%20%20%20%20this.%24loading.start()%3B%0A%20%20%20%20%20%7D%2C%0A%20%20%20%20%20setSpinner%20()%20%7B%0A%20%20%20%20%20%20%20this.%24loading.config(%7B%0A%20%20%20%20%20%20%20%20%20easing%3A%20'ease'%0A%20%20%20%20%20%20%20%7D)%3B%0A%20%20%20%20%20%20%20this.%24loading.start()%3B%0A%20%20%20%20%20%7D%2C%0A%20%20%20%20%20setPercentNum%20()%20%7B%0A%20%20%20%20%20%20%20this.%24loading.config(%7B%0A%20%20%20%20%20%20%20%20%20percentNum%3A%200.1%0A%20%20%20%20%20%20%20%7D)%3B%0A%20%20%20%20%20%20%20this.%24loading.start()%3B%0A%20%20%20%20%20%7D%2C%0A%20%20%20%20%20setShowSpinner%20()%20%7B%0A%20%20%20%20%20%20%20this.%24loading.config(%7B%0A%20%20%20%20%20%20%20%20%20showSpinner%3A%20false%0A%20%20%20%20%20%20%20%7D)%3B%0A%20%20%20%20%20%20%20this.%24loading.start()%3B%0A%20%20%20%20%20%7D%0A%20%20%20%20%7D%0A%20%20%7D%0A%3C%2Fscript%3E%0A%0A"></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">w-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"setSpeed"</span>&gt;</span>设置speed速度<span class="hljs-tag">&lt;/<span class="hljs-name">w-button</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">w-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"setSpinner"</span>&gt;</span>设置spinner动画效果<span class="hljs-tag">&lt;/<span class="hljs-name">w-button</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">w-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"setPercentNum"</span>&gt;</span>设置percentNum每次加载的比例<span class="hljs-tag">&lt;/<span class="hljs-name">w-button</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">w-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"setShowSpinner"</span>&gt;</span>设置是否显示spinner<span class="hljs-tag">&lt;/<span class="hljs-name">w-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span>{
    <span class="hljs-attr">methods</span>: {
     setSpeed () {
       <span class="hljs-keyword">this</span>.$loading.config({
         <span class="hljs-attr">speed</span>: <span class="hljs-number">10</span>
       });
       <span class="hljs-keyword">this</span>.$loading.start();
     },
     setSpinner () {
       <span class="hljs-keyword">this</span>.$loading.config({
         <span class="hljs-attr">easing</span>: <span class="hljs-string">'ease'</span>
       });
       <span class="hljs-keyword">this</span>.$loading.start();
     },
     setPercentNum () {
       <span class="hljs-keyword">this</span>.$loading.config({
         <span class="hljs-attr">percentNum</span>: <span class="hljs-number">0.1</span>
       });
       <span class="hljs-keyword">this</span>.$loading.start();
     },
     setShowSpinner () {
       <span class="hljs-keyword">this</span>.$loading.config({
         <span class="hljs-attr">showSpinner</span>: <span class="hljs-literal">false</span>
       });
       <span class="hljs-keyword">this</span>.$loading.start();
     }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>LoadingBar config参数</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>speed</td>
<td>加载速度</td>
<td>Number</td>
<td>0-100</td>
<td>5</td>
</tr>
<tr>
<td>easing</td>
<td>spinner加载动画</td>
<td>String</td>
<td>linear, ease, cubic-bezier...</td>
<td>贝萨尔曲线值</td>
</tr>
<tr>
<td>percentNum</td>
<td>每次前进的百分比</td>
<td>Float</td>
<td>0-1</td>
<td>Math.random()</td>
</tr>
<tr>
<td>showSpinner</td>
<td>是否显示spinner</td>
<td>Boolean</td>
<td>true, false</td>
<td>是否显示spinner</td>
</tr>
</tbody>
</table>
</section></template>

<script>
  export default{
    mounted () {

    },
    methods: {
      start () {
        this.$loading.start()
      },
      end () {
        this.$loading.end()
      },
      error () {
        this.$loading.error()
      },
      setSpeed () {
        this.$loading.config({
          speed: 10
        })
        this.$loading.start()
      },
      setSpinner () {
        this.$loading.config({
          easing: 'ease'
        })
        this.$loading.start()
      },
      setPercentNum () {
        this.$loading.config({
          percentNum: 0.1
        })
        this.$loading.start()
      },
      setShowSpinner () {
        this.$loading.config({
          showSpinner: false
        })
        this.$loading.start()
      }
    }
  }
</script>


