<template><section>
<h1>Tag 标签</h1>
<hr>
<h3>基础用法</h3>
<p>由<code v-pre="" class="code_inline">type</code>属性来选择tag的类型，也可以通过<code v-pre="" class="code_inline">color</code>属性来自定义背景色。</p>
<div class="demo-block">
  <w-tag>标签一</w-tag>
  <w-tag color="success">标签二</w-tag>
  <w-tag color="info">标签三</w-tag>
  <w-tag color="warning">标签四</w-tag>
  <w-tag color="danger">标签五</w-tag>
</div>

                    <demo-block>
                        <div slot="code" data-desc="%0A%20%20%3Cw-tag%3E%E6%A0%87%E7%AD%BE%E4%B8%80%3C%2Fw-tag%3E%0A%20%20%3Cw-tag%20color%3D%22success%22%3E%E6%A0%87%E7%AD%BE%E4%BA%8C%3C%2Fw-tag%3E%0A%20%20%3Cw-tag%20color%3D%22info%22%3E%E6%A0%87%E7%AD%BE%E4%B8%89%3C%2Fw-tag%3E%0A%20%20%3Cw-tag%20color%3D%22warning%22%3E%E6%A0%87%E7%AD%BE%E5%9B%9B%3C%2Fw-tag%3E%0A%20%20%3Cw-tag%20color%3D%22danger%22%3E%E6%A0%87%E7%AD%BE%E4%BA%94%3C%2Fw-tag%3E%0A%0A"></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">
  <span class="hljs-tag">&lt;<span class="hljs-name">w-tag</span>&gt;</span>标签一<span class="hljs-tag">&lt;/<span class="hljs-name">w-tag</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">w-tag</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"success"</span>&gt;</span>标签二<span class="hljs-tag">&lt;/<span class="hljs-name">w-tag</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">w-tag</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"info"</span>&gt;</span>标签三<span class="hljs-tag">&lt;/<span class="hljs-name">w-tag</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">w-tag</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"warning"</span>&gt;</span>标签四<span class="hljs-tag">&lt;/<span class="hljs-name">w-tag</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">w-tag</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"danger"</span>&gt;</span>标签五<span class="hljs-tag">&lt;/<span class="hljs-name">w-tag</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>可移除标签</h3>
<p>设置<code v-pre="" class="code_inline">closable</code>属性可以定义一个标签是否可移除。默认的标签移除时会附带渐变动画，它接受一个Boolean，true 为关闭。</p>
<div class="demo-block">
<w-tag :key="tag" v-for="tag in dynamicTags" closable="" :disable-transitions="false" @close="handleClose(tag)" :color="tag.color">
  {{tag.name}}
</w-tag>

</div>

                    <demo-block>
                        <div slot="code" data-desc="%0A%3Cw-tag%0A%20%20%3Akey%3D%22tag%22%0A%20%20v-for%3D%22tag%20in%20dynamicTags%22%0A%20%20closable%0A%20%20%3Adisable-transitions%3D%22false%22%0A%20%20@close%3D%22handleClose(tag)%22%20%3Acolor%3D%22tag.color%22%3E%0A%20%20%7B%7Btag.name%7D%7D%0A%3C%2Fw-tag%3E%0A%3Cscript%3E%0Aexport%20default%20%7B%0A%20%20%20%20data()%20%7B%0A%20%20%20%20%20%20return%20%7B%0A%20%20%20%20%20%20%20%20dynamicTags%3A%20%5B%7B%0A%20%20%20%20%20%20%20%20%20%20%20%20name%3A%20'%E6%A0%87%E7%AD%BE%E4%B8%80'%2C%0A%20%20%20%20%20%20%20%20%20%20%20%20color%3A%20'primary'%20%0A%20%20%20%20%20%20%20%20%7D%2C%20%7B%0A%20%20%20%20%20%20%20%20%20%20%20name%3A%20'%E6%A0%87%E7%AD%BE%E4%BA%8C'%2C%0A%20%20%20%20%20%20%20%20%20%20%20color%3A%20'success'%20%0A%20%20%20%20%20%20%20%20%7D%2C%20%7B%0A%20%20%20%20%20%20%20%20%20%20%20name%3A%20'%E6%A0%87%E7%AD%BE%E4%B8%89'%2C%0A%20%20%20%20%20%20%20%20%20%20%20color%3A%20'info'%20%20%20%20%20%20%20%20%20%20%20%0A%20%20%20%20%20%20%20%20%7D%2C%20%7B%0A%20%20%20%20%20%20%20%20%20%20%20%20name%3A%20'%E6%A0%87%E7%AD%BE%E5%9B%9B'%2C%0A%20%20%20%20%20%20%20%20%20%20%20%20color%3A%20'danger'%0A%20%20%20%20%20%20%20%20%7D%5D%0A%20%20%20%20%20%20%7D%3B%0A%20%20%20%20%7D%2C%0A%20%20%20%20methods%3A%20%7B%0A%20%20%20%20%20%20handleClose(tag)%20%7B%0A%20%20%20%20%20%20%20%20this.dynamicTags.splice(this.dynamicTags.indexOf(tag)%2C%201)%3B%0A%20%20%20%20%20%20%7D%0A%20%20%20%20%7D%0A%20%20%7D%0A%3C%2Fscript%3E%0A%0A"></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">w-tag</span>
  <span class="hljs-attr">:key</span>=<span class="hljs-string">"tag"</span>
  <span class="hljs-attr">v-for</span>=<span class="hljs-string">"tag in dynamicTags"</span>
  <span class="hljs-attr">closable</span>
  <span class="hljs-attr">:disable-transitions</span>=<span class="hljs-string">"false"</span>
  <span class="hljs-attr">@close</span>=<span class="hljs-string">"handleClose(tag)"</span> <span class="hljs-attr">:color</span>=<span class="hljs-string">"tag.color"</span>&gt;</span>
  {{tag.name}}
<span class="hljs-tag">&lt;/<span class="hljs-name">w-tag</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">dynamicTags</span>: [{
            <span class="hljs-attr">name</span>: <span class="hljs-string">'标签一'</span>,
            <span class="hljs-attr">color</span>: <span class="hljs-string">'primary'</span> 
        }, {
           <span class="hljs-attr">name</span>: <span class="hljs-string">'标签二'</span>,
           <span class="hljs-attr">color</span>: <span class="hljs-string">'success'</span> 
        }, {
           <span class="hljs-attr">name</span>: <span class="hljs-string">'标签三'</span>,
           <span class="hljs-attr">color</span>: <span class="hljs-string">'info'</span>           
        }, {
            <span class="hljs-attr">name</span>: <span class="hljs-string">'标签四'</span>,
            <span class="hljs-attr">color</span>: <span class="hljs-string">'danger'</span>
        }]
      };
    },
    <span class="hljs-attr">methods</span>: {
      handleClose(tag) {
        <span class="hljs-keyword">this</span>.dynamicTags.splice(<span class="hljs-keyword">this</span>.dynamicTags.indexOf(tag), <span class="hljs-number">1</span>);
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
</div></demo-block>
<h2>API</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>name</td>
<td>用于触发关闭事件时的回调</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>color</td>
<td>类型</td>
<td>String</td>
<td><code v-pre="" class="code_inline">primary</code>, <code v-pre="" class="code_inline">success</code>, <code v-pre="" class="code_inline">error</code>, <code v-pre="" class="code_inline">warning</code>, <code v-pre="" class="code_inline">info</code></td>
<td>primary</td>
</tr>
<tr>
<td>closable</td>
<td>是否可关闭</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
</tbody>
</table>
<h2>Tag 事件</h2>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>close</td>
<td>点击关闭按钮时触发</td>
<td>event</td>
</tr>
</tbody>
</table>
</section></template>
<style>
  .w__tag{
    margin-right: 10px;
  }
</style>
<script>
export default {
    data() {
      return {
        dynamicTags: [{
           name: '标签一',
           color: 'primary' 
        }, {
           name: '标签二',
           color: 'success' 
        }, {
           name: '标签三',
           color: 'info'           
        }, {
            name: '标签四',
            color: 'danger'
        }]
      };
    },
    methods: {
      handleClose(tag) {
        this.dynamicTags.splice(this.dynamicTags.indexOf(tag), 1);
      }
    }
  }
</script>


